*{
  margin: 0;
  font-family: Monospace;
  padding: 0;
  font-size: 20px;
}

body{
  background-color: greenyellow;

}

.container{
  background-color: #DBE2EF;
}

.top-page{
  width: 100%;
  height: 500px;
  background-color: white;

}
table{
  border: 1px black;
  width: 100%;
}

.greeting{
  display: flex;
  align-items: center;
  width: 500px;
  background-color: #112D4E;
  color: white;
}

.greeting h1{
  font-size: 120px;
}

.top-pic{
  display: flex;
  align-items: center;
  justify-content: center;
}

.top-pic img{
  display: flex;
  align-items: center;
  justify-content: center;    
  width: 500px;
  margin-left: 90px;
  margin-right: 0;
}

/* .name{
  background-color: red;
} */

.name p{
  font-size: 40px;

}


.p1{
  /* border-width: 2px;
  border-style: solid;
  border-color: greenyellow; */
  margin-bottom: 0;
}

.p2{
  margin-bottom: 50px;
}



.read-more:hover{
  cursor: pointer;
  background-color: #DBE2EF;
}


a{
  color: white;
  text-decoration: none;
  font-size: 13px;
}

table tbody tr td .read-more{
  display: flex;
  align-items: center;
  justify-content: center;
  width: 120px;
  height: 50px;
  background-color: #112D4E;
  border-radius: 20px;
  color: white;
}

.read-more p {
  font-size: 15px;
}
